  <div class="user">
    <div class="header">
      <van-image
          round
          width="0.6rem"
          height="0.6rem"
          :src="$serverUrl+data.icon"
      />
      <div class="user-box">
        <div class="reg_user">
          <span>{{ data.nikename }}</span>
        </div>
        <van-button size="mini" color="transparent" class="reg_btn" @click="exit">注销</van-button>
      </div>
      <ul class="clear">
        <li>
          <span>0</span>
          <p>关注</p>
        </li>
        <i></i>
        <li>
          <span>0</span>
          <p class="end">粉丝</p>
        </li>
      </ul>
    </div>

    <div class="biaoqian">
      <span><i class="iconfont">&#xe69e;</i><em>消息通知</em></span>
      <span><i class="iconfont">&#xeca7;</i>收藏</span>
      <span><i class="iconfont">&#xe621;</i>浏览记录</span>
      <span><i class="iconfont">&#xe792;</i>下载管理</span>
    </div>
    <div class="jilu">
      <span>记录新鲜事</span>
      <a>+ 发布</a>
    </div>
        <div class="docList">
          <div class="docList_top">
            <van-cell title="公开博文" is-link value="0" class="docList-sytle"/>
            <van-cell title="秘密博文" is-link value="0" class="docList-sytle"/>
            <van-cell title="草稿箱" is-link value="0" class="docList-sytle"/>
            <van-cell title="收藏夹" is-link value="0" class="docList-sytle"/>
          </div>
        </div>

  </div>
</template>

<script>
import '../assets/fonts/iconfont.css'

export default {
  name: "user",
  data() {
    return {
      data: {}
    }
  },
  methods: {
    exit() {
      document.cookie = "token=aaa;expires=-1";
      this.$router.push("/login")
    }
  },
  beforeCreate() {
    let data = document.cookie.split('; ').reduce((obj, value) => ({
      ...obj,
      [value.split("=")[0]]: value.split("=")[1]
    }), {})
    this.$axios({
      url: '/user',
      type: 'get',
      params: {
        token: data.token
      }
    }).then(res => {
      if (res.data.err) {
        console.log(res.data.title)
      } else {
        this.data = res.data.data
      }
    })
  }
}
</script>
